'use strict';

// MAIN //

/**
* Copies the elements of an array-like object to a new array.
*
* @private
* @param {Collection} x - source array
* @returns {Array} output array
*/
function copy( x ) {
	var out;
	var i;

	out = [];
	for ( i = 0; i < x.length; i++ ) {
		out.push( x[ i ] );
	}
	return out;
}


// EXPORTS //

export default copy;
